	module.exports = {"data":{"markdownRemark":{"html":"<p>Today is a quick post on adding that personal touch to your Windows 7 PC, changing the start menu orb!</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 552px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 38.768115942028984%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"start orb\"\n        title=\"\"\n        src=\"/static/6d02f80337bf564be92b91b5132add92-05376.png\"\n        srcset=\"/static/6d02f80337bf564be92b91b5132add92-a6dfe.png 148w,\n/static/6d02f80337bf564be92b91b5132add92-baf6d.png 295w,\n/static/6d02f80337bf564be92b91b5132add92-05376.png 552w\"\n        sizes=\"(max-width: 552px) 100vw, 552px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>There’s not a lot you can customize on a Windows 7 desktop so I quite like the touch that a different start button orb gives and can be something that grabs the attention of anyone who uses your computer.</p>\n<p>Swapping the orb out for a new one couldn’t be easier, with the help of a easy to use program created by a user at <a href=\"http://thewindowsclub.com\">thewindowsclub.com</a> you can quickly backup your current orb and try a new one. The program comes with a few sample orbs and you can get it from the download link at the bottom of the page.</p>\n<p>Now you may find that the few replacements  that come with said program aren’t unique enough well lucky enough users over at <a href=\"http://browse.deviantart.com/customization/?q=start%20orb&#x26;order=9&#x26;offset=0\">DeviantArt</a> have made a whole range of unique orbs for you to try.</p>\n<p>And if you can’t find what you’re looking for you can always create your own,  ZaliTH over at <a href=\"http://www.sevenforums.com/\">sevenforums</a> wrote a really in-depth <a href=\"http://www.sevenforums.com/tutorials/73616-how-create-custom-start-orb-image.html\">tutorial</a> on how to do exactly that, it involves creating an image with the 3 steps that make up a start orb, static, hover and active. This can be seen in the following example of a start orb replacement image.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 54px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 309.25925925925924%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"start orb 2\"\n        title=\"\"\n        src=\"/static/486119ee1ec1525306a714e8124500ec-4e778.png\"\n        srcset=\"/static/486119ee1ec1525306a714e8124500ec-4e778.png 54w\"\n        sizes=\"(max-width: 54px) 100vw, 54px\"\n      />\n    </span>\n  </span>\n  </p>\n<p><a href=\"http://www.thewindowsclub.com/windows-7-start-button-changer-released\">DOWNLOAD WINDOWS 7 START BUTTON CHANGER</a></p>","frontmatter":{"date":"September 11, 2012","path":"/start-orb.html","tags":["tag"],"title":"Start Orb","image":{"childImageSharp":{"resize":{"src":"/static/4e9a95cf7025f7dc1da351e45e9f97b3-57c0c.jpg"}}}}}},"pathContext":{"prev":{"excerpt":"GSOC is officially over, you can check out what my mentor thinks  here . Next step, support and finishing those annoying last bits that were beyond me at the time.","html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"pandroid icon\"\n        title=\"\"\n        src=\"/static/55a16a07f6632096a06e6c2e7f8357f9-adfc8.png\"\n        srcset=\"/static/55a16a07f6632096a06e6c2e7f8357f9-b4fb9.png 148w,\n/static/55a16a07f6632096a06e6c2e7f8357f9-ab48e.png 295w,\n/static/55a16a07f6632096a06e6c2e7f8357f9-adfc8.png 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>GSOC is officially over, you can check out what my mentor thinks <a href=\"http://blog.pandorafms.org/?p=605\">here</a>.</p>\n<p>Next step, support and finishing those annoying last bits that were beyond me at the time.</p>","id":"/Users/markholland/Documents/partiallogic-gatsbyJS/posts/2012-09-17-phase-one-complete/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2012-09-17T12:00:00+00:00","path":"/phase-one-complete","tags":["tag"],"title":"Phase one complete"}},"next":{"excerpt":"From SourceForge blogs:  August 13th was the the “soft pencils down” date for the Google Summer of Code, with a hard end date of August 24th. So I contacted the SourceForge projects that were involved, to see how their summer went. Some of them are…","html":"<p>From SourceForge blogs:</p>\n<p> August 13th was the the “soft pencils down” date for the Google Summer of Code, with a hard end date of August 24th. So I contacted the SourceForge projects that were involved, to see how their summer went.</p>\n<p>Some of them are still hard at it, and expect to go all the way up to the hard end date, but a few were ready to talk about how great their summer was.</p>\n<p>I heard from Sancho Lerena of the Pandora FMS project, who told me that the summer went very well.</p>\n<ul>\n<li>\n<p>Give us an overview of what your project is.</p>\n<p>Pandora FMS is an all-purpose monitoring system, but very oriented to big sites ( > 1000 nodes). It’s very very flexible and able to get information from almost any kind of source, and produce useful reports, alerts, graphs, etc.</p>\n</li>\n<li>\n<p>What was your student’s assignment?</p>\n<p>We try to use the student time in small and isolated tasks, or subprojects. We have three students and put each of them in different tasks, so they get interaction only with other developers in the project. They have been evolving a event viewer for Android and the Android agent, adding some features and increasing the usability and stability. Our other student has developed (from scratch) a Chrome and Firefox extension to show monitoring events by using our Web API.</p>\n</li>\n<li>\n<p>Was the assignment completed to your satisfaction?</p>\n<p>VERY satisfied.</p>\n</li>\n<li>\n<p>When can we expect to see the fruits of the summer’s work in a released version?</p>\n<p>Of course, all of three subjects has been produced useful code and will be used for next version, probably even before, because all of them are using interfaces and/or API compatible with current version, so, in a few weeks probably we’ll release something. All of this time they have been working with our SVN server, on Sourceforge, of course </p>\n</li>\n<li>\n<p>Would you participate in GSoC again?</p>\n<p>Will be a pleasure.</p>\n</li>\n<li>\n<p>What advice can you give to a project interested in GSoC for next year?</p>\n<p>Try to locate small parts of the project, something which don’t require learning a full API, a full architecture or Database design. Do a “roadmap” based on isolated subtasks, so if you there are any delays, you can remove some points, but have something functional at the end. If you plan five subtasks, but at the end you get only three, but these three are OK – you’ve got something to evolve and do more things in the future.</p>\n<p>We had good luck, and two of the students (one from Spain, and other from UK) were near our main office, so we organized a small meeting at the beginning, That was useful to meet people and talk about what we all expected – much better than an IRC Meeting. With the other student, from Singapore, gtalk was the best tool to keep updated on progress and give tips.\n</p>\n</li>\n</ul>\n<p><a href=\"http://sourceforge.net/blog/pandora-fms-and-gsoc/\">Source</a></p>","id":"/Users/markholland/Documents/partiallogic-gatsbyJS/posts/2012-08-21-pandorafms-and-gsoc/index.md absPath of file >>> MarkdownRemark","timeToRead":2,"frontmatter":{"date":"2012-08-21T12:00:00+00:00","path":"/pandora-fms-gsoc.html","tags":["tag"],"title":"PandoraFMS and GSoC"}}}}

